//    1151px 

@media screen and (max-width: 1150px){//   
    .container{
        width: 582px;
        max-width: calc(100% - 36px * 2);
    }
    .container--long{//    .container  .hr     
        width: 697px;
    }
}

//     


@media screen and (max-width: 767px ){// 767  .     .      360
    .container{// -    -      
        width: vw(333);
        max-width: calc(100% - #{vw(14)} * 2);
    }
    .container--hr{//    .container  .hr
        width: vw(333);
    }
}